body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.App {
  background: linear-gradient(#03a9f4, #4caf50);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.container {
  background-color: #009688;
  height: 70vh;
  padding: 100px 50px 0 50px;
  margin: auto;
}

input {
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid #fff;
  width: 100%;
  display: block;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 4px;
  font-weight: 100;
  letter-spacing: 1px;
  font-size: 15px;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  outline: none;
}

.info {
  width: 100%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.info p {
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.06);
  font-weight: 400;
}

.info p:last-of-type {
  border: 0;
}